.constructor {
  display: flex;
  flex-direction: column;
  min-width: 600px;
  box-sizing: border-box;
  height: 100%;
}

.constructor__text {
  align-self: center;
  text-align: center;
  white-space: pre-wrap;
}

.constructor__container {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  max-height: calc(100% - 100px);
}

.constructor__scroll {
  display: flex;
  flex-direction: column;
  width: 100%;
  gap: 16px;
  list-style: none;
  padding: 0;
  overflow-y: auto;
}

.constructor__scroll::-webkit-scrollbar {
  width: 8px;
  background-color: #2f2f37;
}

.constructor__scroll::-webkit-scrollbar-thumb {
  background-color: #8585ad;
  border: 3px solid #8585ad;
  box-sizing: border-box;
}

.constructor__confirm {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.constructor__price {
  display: flex;
  align-items: center;
}

.constructor__price-value {
  margin-right: 8px;
}
