.layout {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 100vh;
}

.main {
  display: flex;
  height: calc(100vh - 100px);
  width: 100%;
  max-width: 1280px;
  justify-content: center;
}
